'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'

const navItems = [
  { label: 'HOME', href: '/' },
  { label: 'BIO', href: '/bio' },
  { label: 'PORTFOLIO', href: '/selected-works' },
  { label: 'EXHIBITIONS', href: '/exhibitions' },
  { label: 'CONTACT', href: '/contact' },
]

export default function Navigation() {
  const pathname = usePathname()
  const [isOpen, setIsOpen] = useState(false)
  const [isArchitectural, setIsArchitectural] = useState(false)
  const isHome = pathname === '/'

  // Listen for theme changes
  useEffect(() => {
    const checkTheme = () => {
      setIsArchitectural(document.body.classList.contains('theme-architectural'))
    }
    checkTheme()
    
    // Watch for class changes on body
    const observer = new MutationObserver(checkTheme)
    observer.observe(document.body, { attributes: true, attributeFilter: ['class'] })
    
    return () => observer.disconnect()
  }, [])

  // Architectural Theme: Fixed Left Sidebar
  if (isArchitectural) {
    return (
      <nav style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '220px',
        height: '100vh',
        backgroundColor: '#1a1a1a',
        zIndex: 120,
        display: 'flex',
        flexDirection: 'column',
        padding: '60px 30px',
        boxSizing: 'border-box'
      }}>
        {/* Artist Name */}
        <Link href="/" style={{
          fontFamily: "'Courier New', monospace",
          fontSize: '18px',
          letterSpacing: '0.15em',
          color: '#ffffff',
          textDecoration: 'none',
          marginBottom: '60px',
          fontWeight: 400
        }}>
          CHELSEY L.
        </Link>

        {/* Nav Links */}
        <ul style={{
          listStyle: 'none',
          margin: 0,
          padding: 0,
          display: 'flex',
          flexDirection: 'column',
          gap: '20px'
        }}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                style={{
                  fontFamily: "'Courier New', monospace",
                  fontSize: '16px',
                  letterSpacing: '0.1em',
                  textDecoration: 'none',
                  color: pathname === item.href ? '#ffffff' : 'rgba(255,255,255,0.5)',
                  transition: 'color 0.3s ease',
                  textTransform: 'uppercase'
                }}
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>

        {/* Footer info */}
        <div style={{
          marginTop: 'auto',
          fontFamily: "'Courier New', monospace",
          fontSize: '13px',
          color: 'rgba(255,255,255,0.3)',
          letterSpacing: '0.05em'
        }}>
          Melbourne, AU
        </div>
      </nav>
    )
  }

  // Jade Theme: Hamburger + Right Slide-in (Original)
  return (
    <>
      <nav style={{ 
        position: 'fixed', 
        top: 0, 
        left: 0, 
        right: 0, 
        zIndex: 120, 
        padding: '40px 60px',
        display: 'flex',
        justifyContent: 'flex-end',
        backgroundColor: 'transparent',
        pointerEvents: 'none'
      }}>
        {/* Hamburger Icon */}
        {!isOpen && (
          <button 
            onClick={() => setIsOpen(true)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              display: 'flex',
              flexDirection: 'column',
              gap: '6px',
              pointerEvents: 'auto',
              padding: '10px'
            }}
          >
            <div style={{ width: '30px', height: '1.5px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
            <div style={{ width: '30px', height: '1.5px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
          </button>
        )}
      </nav>

      {/* Electric Blue Sidebar Menu (Jade style) */}
      <div style={{
        position: 'fixed',
        top: 0,
        right: 0,
        width: '50vw',
        height: '100vh',
        backgroundColor: '#0000FF',
        zIndex: 150,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'center',
        paddingLeft: '8vw',
        transform: isOpen ? 'translateX(0)' : 'translateX(100%)',
        transition: 'transform 0.6s cubic-bezier(0.19, 1, 0.22, 1)',
      }}>
        {/* Close button (X) */}
        <button 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'absolute',
            top: '40px',
            right: '60px',
            background: 'none',
            border: 'none',
            cursor: 'pointer',
            padding: '10px'
          }}
        >
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M1 1L23 23M23 1L1 22.5" stroke="white" strokeWidth="2"/>
          </svg>
        </button>

        <ul style={{ 
          display: 'flex', 
          flexDirection: 'column',
          alignItems: 'flex-start',
          gap: '2.5vw', 
          listStyle: 'none', 
          margin: 0, 
          padding: 0 
        }}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                onClick={() => setIsOpen(false)}
                style={{
                  fontSize: 'clamp(28px, 5vw, 56px)',
                  letterSpacing: '0.15em',
                  fontWeight: 700,
                  textTransform: 'uppercase',
                  textDecoration: 'none',
                  color: '#ffffff',
                  fontFamily: 'ui-sans-serif, system-ui, -apple-system, sans-serif',
                  transition: 'opacity 0.3s ease',
                  opacity: pathname === item.href ? 1 : 0.7
                }}
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      {/* Background Dimmer */}
      {isOpen && (
        <div 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'fixed',
            inset: 0,
            backgroundColor: 'rgba(0,0,0,0.1)',
            zIndex: 140,
          }}
        />
      )}
    </>
  )
}
